/* slider */
.container-slider{
    margin-top: -70px;
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    scroll-behavior: smooth;
} 
    button[class*='btn-slider-']{
        position: absolute;
        width: 50px;
        height: 50px;
        background: none;
        border: none;
        font-size: 4vw;
        color: var(--color-blanco);
        opacity: 60%;
        cursor: pointer;
    }
    
        button[class*='btn-slider-']:hover{
            opacity: 100%;
            text-shadow: 0px 0px 10px var(--color-blanco);
        }
        
        .btn-slider-previous{
            left: 0;
            margin-left: 20px;
        }
        .btn-slider-next{
            margin-right: 20px;
            right: 0;
        }
.size-slider{
    position: absolute;
    left: 0;
    right: 0; 
    bottom: 5%;

    display: flex;
    justify-content: center;
}
    .size-slider>.icon{
        width: 13px;
        height: 13px;
        border-radius: 50%;
        border: none;
        margin: 0 5px;
        background: var(--color-blanco);
        opacity: 50%;
        cursor: pointer;

        transition: all ease .3s;
    }
    .size-slider .icon:hover{
        box-shadow: 0px 0px 18px -1px rgba(255,255,255,1);
    }
    .size-slider .icon.active, .size-slider .icon:hover{
        opacity: 100%;        
    }
.container-slider .txt-slider{
    width: 40%;
    margin: 0 0 0 10%;
    color: var(--color-blanco);
}

    .hero__titulo{
        font-size: 4vw;
        margin: -10px 0 0 0;
        font-family: var(--fuente-FRANKLIN);
        font-weight: var(--weigth-Flin-900);
    }
        .grand__text{
            font-size: 10.5vw;
            line-height: 90%;
        }
    
    .hero__subtitulo{
        margin: 0;
        font-size: 2vw;
        display: flex;
        font-family: var(--fuente-OPENSANS);
        font-weight: var(--weigth-OPENSANS-LIGTH-300);
    }
    .hero__subtitulo.text{
        font-weight: var(--weigth-OPENSANS-REGULAR-400);
        font-size: 1.3vw;
    }
.slider-pictures{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;

    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    overflow: hidden;
    background-color: rgba(0, 0, 0);
}
    .slider-pictures .slide{
        max-width: 0;
        opacity: 0;
        transition-duration: 1s ease;
    }
        .slider-pictures .slide.active{
            height: 100%;
            min-width: 100%;
            opacity: 1;
            transition-duration: 1s;
            -webkit-transform: scale(1.07);
                    transform: scale(1.07);
        }
    .slider-pictures .slide>img{
        width: 100%;
        height: 100%;
        opacity: 60%;
        object-fit: cover;
    }



/* PROMOCIONES */
.promo-general{
    background-color: var(--color-blanco);
    font-family: var(--fuente-OPENSANS);
    padding: 50px 0;
}
.head_main_section{
    display: flex;
    padding: 25px 0;
    justify-content: center;
}
.head_main_section>h1{
    margin: 0;
    font-family: var(--fuente-OSWALD);
    color: var(--color-text-negro-bajo);
    font-size: 3rem;
    text-transform: uppercase;
    border-bottom: 8px solid var(--color-amarillo);
}
.contain-promos{
    display: grid;
    width: 90%;
    margin: auto;
    padding-top: 25px;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 250px;
    grid-column-gap: 20px;
}
    .item__promo{
        position: relative;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #000;
        box-shadow: 0 10px 15px var(--color-text-input);        
    }
    .item__promo>img{
        width: 100%;
        height: 250px;
        object-fit: cover;
        opacity: 80%;

        transition: all ease .4s;
    }
    .item__promo .detalle{
        position: absolute;
        bottom: 0;        
        left: 0;
        right: 0;
        font-family: var(--fuente-FRANKLIN);
        padding: 50px 25px 25px 25px;
        z-index: 1;
        background: linear-gradient(to bottom, rgba(31, 37, 57, 0) 0%, var(--color-vino) 100%);
    }
    .item__promo .detalle .topic{
        font-weight: 900;
        color: var(--color-amarillo-principal);
        text-transform: uppercase;
        font-size: 1.5rem;
        margin: 0;
        display: flex;
        justify-content: space-between;
        -webkit-transform: translateY(30px);
                transform: translateY(30px);
        
        transition: all ease .4s;
    }
    .item__promo .detalle .descrip{
            opacity: 0;
            font-size: 1.8rem;
            margin: 0;
            color: var(--color-amarillo-principal);
            display: flex;
            justify-content: space-between;
            
            transition: all ease .5s;
            /* transition-delay: .2s; */
        }
            .item__promo .detalle .descrip s{
                color: var(--color-blanco);
                font-size: 1rem;
                justify-content: space-between;
            }
            
        .item__promo:hover{ 
            box-shadow: 0 10px 15px var(--color-text-negro);
        }
        .item__promo:hover img{ 
            height: 300px;
        }
        .item__promo:hover .topic{
            -webkit-transform: translateY(0);
                    transform: translateY(0);
        }
        .item__promo:hover .descrip{
            opacity: 1;
        } 

.more_promo{
    width: 90%;
    margin: 0 auto;
    text-align: center;
    padding-top: 70px;
}
    .more_promo .btn_more_promo{
        border: 2.5px solid var(--color-vino);
        color: var(--color-vino);
        font-weight: 900;
        border-radius: 40px;
        padding: 15px 20px;
        text-decoration: none;
        transition: all ease .5s;
        font-size: .9rem;
    }
    .btn_more_promo:hover{
        background-color: var(--color-vino);
        color: var(--color-blanco);
        font-size: 1rem;
    }


/* ASIDE DELIVERY */
.aside-delivery{
    margin-top: 40px;
    width: 100%;
    height: 375px;
}
    .aside-delivery .over{
        background-color: rgba(0, 0, 0);
        width: 100%;
        height: 375px;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .content-infor{
        position: absolute;
        font-family: var(--fuente-FRANKLIN);
        color: var(--color-blanco);
        z-index: 1;
    }
        .content-infor .theme{
            display: flex;
            align-items: center;
            justify-content:center;
        }
        .theme>p{
            width: 200px;
            text-align: center;
        }
        .theme>hr{
            border: 2px solid var(--color-amarillo-principal);
            width: 100px;
            border-radius: 5px;
            margin: 0;
        }
    .detalle_delivery{
         text-align: center;
    }
    .detalle_delivery .detalle-sms{
        font-size: 1.7rem;
        font-weight: 900;
        margin: 0;  
    }
    .detalle_delivery .detalle-sms>span{
        text-transform: uppercase;
    }
    .detalle_delivery .price{
        color: var(--color-amarillo-principal);
        font-size: 2.2rem;
        margin: 0;
        padding: 10px 0;
    }
    .detalle_delivery .price>span{
        position: absolute;
        float: top;
        font-size: 1.4rem;
    }
    .detalle_delivery >img{
        width: 150px;
    }
    .over .public-aside{
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
    }
        .over .public-aside>img{
            min-width: 100%;
            min-height: 100%;
            object-fit: cover;
            opacity: 50%;
        }


/*Edit Mendoza xd*/
