/*---------------------------NUESTRO MENU--------------------------------*/
.menu__category{
    background: var(--color-amarillo-principal);
    padding: 50px 0;
}
.section_carrousel{
    font-family: var(--fuente-OPENSANS);
    display: flex;
    justify-content: center;
    align-items: center;
}
.section_carrousel>button{
    width: 40px;
    height: 40px;
    background: none;
    color: var(--color-blanco);
    font-size: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    cursor: pointer;
    border-radius: 50%;

    transition: background ease .2s;
}
    .section_carrousel button:hover{
        color: var(--color-vino);
    }
.group_carrousel{
    display: flex;
    margin: 0 25px;
    overflow: hidden;
    scroll-behavior: smooth;
    max-width: 870px;
}
.group_carrousel .item-cat_menu{
    padding: 30px;
    text-align: center;
    text-decoration: none;
}
    .item-cat_menu .item-img{
        width: 200px;
        height: 200px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        overflow: hidden;
        padding: 15px;
    }
    .item-cat_menu .item-img>img{
        width: 75%;
        max-height: 75%;
        object-fit: cover;
        position: absolute;

        transition: all ease .5s;
    }
        .item-cat_menu:hover .item-img>img{
            width: 100%;
            max-height: 100%;
        }
    .item-cat_menu .name-cat{
        text-transform: uppercase;
        font-weight: 900;
        font-size: 1.1rem;
        margin: 0;
        padding-top: 25px;
        color: var(--color-blanco);

        transition: all ease .2s;
    }
        .item-cat_menu:hover .name-cat::before{
            color: var(--color-vino);
            border-bottom: 2px solid var(--color-vino);
        }     





/*------------------------RESPONSIVIDAD---------------------*/
@media only screen and (max-width: 960px) {
    /* -----SLIDER-------- */
    .container-slider{
        margin-top: 60px;
        height: 70vh;
    }
        .hero__subtitulo{
            font-size: 2.2vw;
        }
        .hero__titulo{
            font-size: 4.5vw;
        }
        .grand__text{
            font-size: 11.5vw;
        }
        .hero__subtitulo.text{
            font-size: 1.5vw;
        }
    /*------------PROMOCIONES------*/
    .contain-promos{
        width: 60%;
        grid-template-rows: repeat(3, 300px);
        margin: 0 auto;
        grid-template-columns: 1fr;
        grid-row-gap: 30px;
    }
    .item__promo>img{
        height: 350px;
    }
    .item__promo:hover img{ 
        height: 400px;
    }
    /*------------NUESTRO MENU------*/
    .group_carrousel .item-cat_menu{
        padding: 30px 10px
    }
    .item-cat_menu .item-img{
        width: 165px;
        height: 165px;
        padding: 10px;
    }
    .group_carrousel{
        max-width: 615px;
    }

}
@media only screen and (max-width: 720px) {
    /*-------SLIDER--------*/    
    .container-slider{
        height: 60vh;
    }
        button[class*='btn-slider-']{
            font-size: 5vw;
        }
        .size-slider{
            bottom: 4%;
        }
        .container-slider .txt-slider{
            width: 60%;
            margin: 0 auto;
            text-align: center;
        }
        .hero__subtitulo{
            text-align: center;
            display: block;
            font-size: 1.5rem;
        }
            .hero__subtitulo.text{
                font-size: .95rem;
            }
        .hero__titulo{
            font-size: 3rem;
            color: var(--color-amarillo-principal);
        }
        .grand__text{
            font-size: 6rem;
            line-height: 100%;
        }

    .group-slider{
        height: 60vh;
        background-attachment: local;
    }   
    /*-----------PROMOCIONES-------*/
    .head_main_section>h1{
        font-size: 2rem;
    }
    .contain-promos{
        width: 70%;
    }
    /*------------NUESTRO MENU------*/
    .menu__category{
        padding: 30px 0 50px 0;
    }
    .group_carrousel{
        max-width: 360px;
    } 
    .group_carrousel .item-cat_menu{
        padding: 20px 10px;
    }
    .item-cat_menu .item-img{
        width: 150px;
        height: 150px;
        padding: 5px;
    }
    .item-cat_menu .name-cat{
        font-size: .9rem;
        padding-top: 15px;
    }
}
@media only screen and (max-width: 540px) {
    /* ---------------SLIDER------------- */
    button[class*='btn-slider-']{
        width: 20px;
        height: 20px;
        font-size: 1.7rem;
    }
        .btn-slider-previous{
            margin-left: 10px;
        }
        .btn-slider-next{
            margin-right: 10px;
        }
        .container-slider .txt-slider{
            width: 80%;
        }
        .hero__subtitulo{
            font-size: 1.3rem;
        }
            .hero__subtitulo.text{
                width: 80%;
                margin: 0 auto;
                font-size: .8rem;
            }
        .hero__titulo{
            font-size: 2.4rem;
        }
        .grand__text{
            font-size: 5rem;
        }
    /* ---------PROMOCIONES-------- */
    .contain-promos{
        grid-template-rows: repeat(3, 200px);
    }
     /*-------------ASIDE-----------*/
     .theme>p{
         width: 150px;
         font-size: .8rem;
        }
        .theme>hr{
            width: 30px;
        }
        .detalle_delivery .detalle-sms{
            font-size: 1.3rem;
        }
        .detalle_delivery .detalle-sms>span{
            display: block;
            width: 100%;
            font-size: 1.5rem;
        }
    /*-------------NUESTRO MENU-----------*/
    .menu__category{
        padding: 20px 0 40px 0;
    }
    .group_carrousel{
        margin: 0 15px;
        max-width: 180px;
    }
    
}
/*VARIABLES DE FUENTES, COLOR, ETC QUE SE USAN MULTIPLES VECES*/
:root{
    --color-text-negro: #000103;
    --color-text-negro-bajo: #212529;
    --color-text-input: #707070;
    --color-brd-input: #7070708c;
    --color-blanco: #ffff;
    --color-vino: #370617;
    --color-vino-50: #3706177c;
    --color-amarillo: #f48c06;
    --color-amarillo-principal: #ffba08;
    --color-naranja: #e85d04;
    --color-naranja-bajo: #f48c08;
    --color-rojo: #d00000;
    --color-rojo-oscuro: #9d0208;
    --color-azul-fuerte: #03071E;

    --bg-admin: #E2E7F1;
    --bg-admin-hv: #d3d7dd;


    --amarillo: #f48c06;
    --azul-fuerte: #03071E;
    --azul-opacity-50: #03071e88;
    --blanco: #ffff;
    --bg-dark: #212529;
    --color-aprobado: #38b000;
    --color-reprobado: #c1121f;
    --color-submit: rgb(0, 74, 171);


    /* FRANKLIN FUENTE */
    --fuente-FRANKLIN: 'Libre Franklin', sans-serif;
        --weight-FKlin-700: 700;
        --weigth-Flin-900: 900;


    /*FUENTE "OSWALD" (GRANDE PRINCIPAL)*/
    --fuente-OSWALD: 'Oswald', sans-serif;
    --weigth-OSWALD-LIGTH-300: 300;             /* ANCHO DE LETRA FINA DE OSWALD */
    --weigth-OSWALD-REGULAR-400: 400;           /* ANCHO DE LETRA NORMAL DE OSWALD */
    --weigth-OSWALD-SEMIBOLD-600: 600;          /* ANCHO DE LETRA SEMI ANCHA DE OSWALD */
    --weigth-OSWALD-BOLD-700: 700;              /* ANCHO DE LETRA ANCHA DE OSWALD */


    /*FUENTE "OPEN SANS" (PARA TEXTOS. PARRAFOS O ETIQUETAS)*/
    --fuente-OPENSANS: 'Open Sans', sans-serif;
    --weigth-OPENSANS-LIGTH-300: 300;             /* ANCHO DE LETRA FINA DE OSWALD */
    --weigth-OPENSANS-REGULAR-400: 400;           /* ANCHO DE LETRA NORMAL DE OPEN SANS */
    --weigth-OPENSANS-SEMIBOLD-600: 600;          /* ANCHO DE LETRA SEMI ANCHA DE OPEN SANS */
    --weigth-OPENSANS-BOLD-700: 700;              /* ANCHO DE LETRA ANCHA DE OPEN SANS */
}


