/*-----------MENSAJE DE ADD O NOT ADD A LA BOLSA*/
.box-sms{
    background-color:#fff;
    color: #000103;
    color: var(--color-text-negro);
    font-family: 'Open Sans', sans-serif;
    font-family: var(--fuente-OPENSANS);
    font-weight: 600;
    font-weight: var(--weigth-OPENSANS-SEMIBOLD-600);
    text-transform: uppercase;
    text-align: center;
    padding: 10px 20px;
    border-radius: 7px;
    box-shadow: 2px 2px 10px #000;
    transition: all ease .5s;
    
    position: fixed;
    top: 80px;
    right: 20px;
    z-index: 100;
    visibility: hidden;
}
    .box-sms.active{
        visibility: visible;
    }
    .txt-sms{
        display: flex;
        align-items: center;
    }
    .txt-sms.aprobado>i{
        padding-left: 10px;
        color: #38b000;
        color: var(--color-aprobado);    
    }
    .txt-sms.reprobado>i{
        padding-left: 10px;
        color: #c1121f;
        color: var(--color-reprobado);    
    }
    .box-sms .lay{
        width: 12px;
        height: 12px;
        background-color: #fff;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
        
        position: absolute;
        top: -5px;
        right: 30px;
    }


/* =============================================================
            ESTILOS PARA FORMULARIOS USUARIO
============================================================= */
.container_fields_us {
    flex: 1 1 auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 30px;
    font-family: 'Open Sans', sans-serif;
    font-family: var(--fuente-OPENSANS);
}
.container_fields_us p[class*=field_u]{
    padding-bottom: 15px;
    margin: 0;
    grid-column-start: span 2;
}
    .container_fields_us .field_u.div{
        grid-column-start: span 1;
    }
.field_u label{
    display: block;
    color: #212529;
    color: var(--color-text-negro-bajo);
    font-weight: 400;
    font-weight: var(--weigth-OPENSANS-REGULAR-400);
    font-size: .85rem;
    padding-bottom: 10px;
}
.field_u input, textarea{
    display: block;
    width: calc(100% - 30px);
    padding: 7px 15px;
    outline: none;
    border: 1px solid #7070708c;
    border: 1px solid var(--color-brd-input);
    border-radius: 5px;
    background: none;
    color: #212529;
    color: var(--color-text-negro-bajo);
    font-size: .95rem;
}
.field_u .text_area{
	font-family: 'Open Sans', sans-serif;
	font-family: var(--fuente-OPENSANS);
    text-align: left;
    height: 70px;
    display: block;
    resize: none;
}

/* ========================================= */
            /* MENSAJES DE ALERTS */
/* ========================================= */
p[class*=alert_]{
    font-size: .85rem;
    margin: 7px 0 0 0;
    color: #707070;
    color: var(--color-text-input);
    display: flex;
    flex-wrap: wrap;
    line-height: 110%;
    font-weight: 500;
}
    p[class*=alert_send]{color: #38b000;color: var(--color-aprobado)}
    p[class*=alert_error]{color: #c1121f;color: var(--color-reprobado)}

p[class*=content-empty]{
    display: flex;
    width: 100%;
    margin: 50px 0;
    line-height: 100%;
    text-align: center;
    justify-content: center;
    font-size: 1.3rem;
    font-weight: 600;
    color: #707070;
    color: var(--color-text-input);
}
#bg-error{
  background-color: var(--color-amarillo-principal);
  color: var(--color-vibo);
  font-family: var(--fuente-OPENSANS);
  display: flex;
  justify-content: center;
  text-align: center;

  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}

img.image_err{
  position: relative;
  top: 0;
  left: 0; 
  right: 0; 
  margin: 0 auto;
  
  height: 60%;
}
.container-error>div{
  width: 70%;
  margin: auto;
}
.container-error h1{
  font-size: 1.7rem;
  line-height: 100%;
  font-weight: 300;
}
.container-error button{
  margin-top: 15px;
  width: auto;
  border: none;
  cursor: pointer;
  width: 70%;
  background-color: var(--color-vino);
  padding: 10px 15px;
  border-radius: 25px;
  font-family: var(--fuente-OPENSANS);
  font-size: .9rem;
  color: var(--color-blanco);
  font-weight: 600;
